.login {
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: black;
}

.login > img {
    width: 50%;
}

.login > a {
    padding : 20px;
    border-radius: 100px;
    background-color: #1db954;
    font-weight: 700;
    color: white;
    text-decoration: none;
}