.body {
    padding: 30px;
    width: 100%;
    height: 100vh;
    overflow-y: overlay;
    flex: 0.9;
    color: white;
    background: linear-gradient(transparent, rgba(0, 0, 0, 1));
    background-color: rgb(91, 87, 115);
  }
  
  .body::-webkit-scrollbar {
    display: none;
  }
  
  .body__info {
    display: flex;
    align-items: flex-end;
    padding: 10px;
  }
  
  .body__shuffle {
    font-size: 80px !important;
    margin-left: 50px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  
  .body__icons {
    display: flex;
    align-items: center;
  }
  
  .body__icons > .MuiSvgIcon-root {
    margin-right: 20px;
  }
  
  .body__shuffle:hover {
    transition: 100ms transform ease-in;
    transform: scale(1.08);
  }
  
  .body__background {
    position: absolute;
    top: 13vh;
    right: 0;
    opacity: 0.4;
    height: 80%;
    width: 80%;
    background-color: black;
  }
  
  .body__songs {
    z-index: 1;
    margin: 20px -30px;
  }
  
  .body__info > img {
    height: 20vw;
    margin: 0 20px;
    box-shadow: 0 4px 60px rgba(0, 0, 0, 0.5);
  }
  
  .body__infoText {
    flex: 1;
  }
  
  .body__infoText > h2 {
    font-size: 48px;
    margin-bottom: 10px;
    font-size: 96px; 
    line-height: 96px; 
    visibility: visible;
  }
  
  .body__infoText > p {
    font-size: 14px;
  }

  .body__header {
    display: flex;
    margin: 0 0 10px 0;
  }
  .body__header > :first-child {
    flex: 0.2;
    margin: auto;
  }

  .body__header > :last-child {
    flex: 0.2;
  }

  .body__header > :nth-child(2) {
    flex: 0.37;
  }